/* purgecss start ignore */

.btn-primary{
  background:linear-gradient(to right, #26c8bf, #1fa2f9);
  color:#fff;
  transition:background 3s ease
}

.btn-secondary{
  font-family:red Hat Text !important;
  color:#fff;
  background:#00afa5
}

.btn-thirdry{
  color:#000;
  background:rgba(0,0,0,0);
  border:1px solid #00afa5
}

.dark .btn-thirdry{
  color:#fff
}

.btn-white{
  background:#fff;
  box-shadow:0px 0px 18px rgba(0,0,0,.03);
  border-radius:3px;
  color:#4caaac
}

.btn-disabled{
  cursor:not-allowed
}

.public-private-toggle{
  display:flex;
  justify-content:center
}

input[type=radio]+label span{
  transition:background .2s,transform .2s;
  background-color:#fff
}

input[type=radio]+label span:hover,input[type=radio]+label:hover span{
  transform:scale(0.8)
}

input[type=radio]:checked+label span{
  background-color:#3490dc;
  box-shadow:0px 0px 0px 2px #fff inset
}

input[type=radio]:checked+label{
  color:#3490dc
}

.manta-blue{
  color:#4caaac
}

.manta-prime-blue{
  color:#4caaac
}

.manta-green{
  color:#104ea1
}

.manta-gray{
  color:#7e7e7e
}

.manta-dark-gray{
  color:#7e7e7e
}

.text-blue-thirdry{
  color:#5eb1ff
}

.link-text{
  color:#4caaac !important;
  text-decoration:underline
}

.unselectable-text{
  -webkit-touch-callout:none;
  user-select:none
}

.tighter-word-space{
  word-spacing:-1px
}

.wrap-text{
  inline-size:21rem;
  overflow-wrap:break-word
}

.sortable-table-wrapper{
  max-width:1280px
}

.border-5{
  border-width:5px
}

.gradient-border{
  background:linear-gradient(var(--color-bg-fifth) 0 0) padding-box,linear-gradient(to right, #26c8bf, #1fa2f9) border-box;
  border:3px solid rgba(0,0,0,0)
}

.gradient-border.error{
  background:linear-gradient(var(--color-bg-fifth) 0 0) padding-box,linear-gradient(to right, #ec134b, #e4f91f) border-box
}

.frame-box-shadow{
  box-shadow:0px 0px 18px rgba(0,0,0,.03)
}

.field-box-shadow{
  box-shadow:inset 0px 3px 4px rgba(0,0,0,.17)
}

.search-box-shadow{
  box-shadow:inset 0px 3px 4px rgba(0,0,0,.09)
}

.manta-bg-gray{
  background-color:#f2f3f7
}

.dark .manta-bg-gray{
  background-color:#050d32
}

.manta-bg-light-gray{
  background-color:#7e7e7e
}

.manta-bg-secondary{
  background:linear-gradient(180deg, #f5f7ff 0%, #e2e7fc 100%)
}

.manta-bg-thirdry{
  background:#5eb1ff
}

.manta-bg-blue{
  background:linear-gradient(180deg, #59b9ae 0%, #398ba8 100%)
}

.manta-bg-primary-blue{
  background:#4caaac
}

.manta-bg-secondary-blue{
  background:#104ea1
}

.rounded-3{
  border-radius:3px
}

.rounded-7{
  border-radius:7px
}

.btn-hover:hover{
  background:linear-gradient(180deg, #59b9ae 0%, #398ba8 0%)
}

.disabled{
  cursor:not-allowed !important
}

input:-webkit-autofill,input:-webkit-autofill:hover,input:-webkit-autofill:focus,input:-webkit-autofill:active{
  -webkit-box-shadow:0 0 0 1000px #fff inset !important;
  -webkit-background-clip:text
}

input.manta-bg-gray:-webkit-autofill,input.manta-bg-gray:-webkit-autofill:hover,input.manta-bg-gray:-webkit-autofill:focus,input.manta-bg-gray:-webkit-autofill:active{
  -webkit-box-shadow:0 0 0 1000px #f2f3f7 inset !important;
  -webkit-background-clip:text
}

input.bg-secondary:-webkit-autofill,input.bg-secondary:-webkit-autofill:hover,input.bg-secondary:-webkit-autofill:focus,input.bg-secondary:-webkit-autofill:active{
  -webkit-box-shadow:0 0 0 1000px var(--color-bg-secondary) inset !important;
  -webkit-background-clip:text
}

.dark input:-webkit-autofill,.dark input:-webkit-autofill:hover,.dark input:-webkit-autofill:focus,.dark input:-webkit-autofill:active{
  -webkit-background-clip:text;
  -webkit-text-fill-color:#fff !important
}

.dark input.manta-bg-gray:-webkit-autofill,.dark input.manta-bg-gray:-webkit-autofill:hover,.dark input.manta-bg-gray:-webkit-autofill:focus,.dark input.manta-bg-gray:-webkit-autofill:active{
  -webkit-box-shadow:0 0 0 1000px #050d32 inset !important;
  -webkit-background-clip:text
}

.sidebar-menu{
  box-shadow:6px 0px 23px rgba(0,0,0,.11);
  border-radius:0px
}

.sidebar-menu .logo-content .logo{
  width:55px
}

.menu-content__item svg{
  width:28px;
  height:28px
}

.menu-content .active .menu-content__item svg{
  fill:#fff
}

.light .menu-content__item svg{
  width:28px;
  height:28px
}

.light .menu-content .active .menu-content__item svg{
  fill:var(--color-text-primary)
}

.nav-menu-mobile{
  position:fixed;
  top:65px;
  left:0;
  width:65%;
  max-width:350px;
  height:95%;
  transform:translate3d(-100%, 0, 0);
  text-align:left;
  transition:transform .25s ease-in-out
}

.navbar-mobile-overlay{
  box-shadow:inset 6px 0px 23px rgba(0,0,0,.11)
}

.extrinsic-page .events .item.active,.extrinsic-history .events .item.active{
  color:#4caaac;
  border:1px solid #4caaac
}

.navbar{
  min-height:2.5rem
}

.staking-table{
  max-width:1514px
}

.el-select{
  width:100%;
  padding-top:6px;
  padding-bottom:6px;
  color:#fff !important
}

.el-select .el-input .el-input__inner{
  background-color:rgba(255,255,255,0) !important;
  border:0;
  font-size:22px !important;
  padding-left:14px;
  padding-right:0px;
  color:#fff !important;
  text-align:center
}

.el-select .el-input .el-input__icon{
  color:#4caaac !important;
  font-size:11px !important;
  right:10px;
  background-color:#fff;
  width:20px;
  border-radius:50%;
  padding:0;
  height:20px
}

.el-select .el-input ::placeholder{
  color:#fff !important;
  opacity:1;
  font-size:18px;
  margin-bottom:5rem !important
}

.el-select .el-input :-ms-input-placeholder{
  color:#fff !important;
  font-size:18px;
  padding-bottom:.5rem
}

.el-select .el-input ::-ms-input-placeholder{
  color:#fff !important;
  font-size:18px;
  padding-bottom:.5rem
}

.el-select .el-select-dropdown{
  color:#4caaac !important;
  border-radius:2px 2px 10px 10px;
  left:0 !important
}

.el-select .el-select-dropdown .el-scrollbar{
  margin:0 -1px
}

.el-select .el-select-dropdown__list{
  padding:0
}

.el-select .el-select-dropdown__item{
  height:auto !important
}

.el-select .el-select-dropdown__item.selected{
  border-radius:12px;
  background-color:#4caaac !important
}

.el-select .el-select-dropdown__item.hover{
  border-radius:12px
}

.form-select-label{
  left:67px
}

.el-loading-spinner{
  display:flex !important;
  align-items:center;
  justify-content:center
}

.el-loading-spinner .el-loading-text{
  padding:0 20px
}

.el-notification{
  border-radius:10px !important;
  padding:16px !important;
  background-color:var(--color-bg-fifth) !important;
  width:360px !important
}

.el-notification__icon{
  top:12px !important;
  font-size:30px !important
}

.el-notification__content{
  margin-top:5px !important
}

.el-notification__title{
  color:#fff !important;
  display:none
}

.el-notification__closeBtn{
  top:10px !important;
  right:10px !important
}

.visually-hidden{
  position:absolute !important;
  width:1px !important;
  height:1px !important;
  padding:0 !important;
  margin:-1px !important;
  overflow:hidden !important;
  clip:rect(0, 0, 0, 0) !important;
  white-space:nowrap !important;
  border:0 !important
}

.spinner-border{
  vertical-align:-0.125em em;
  border:.25em solid;
  border-right-color:rgba(0,0,0,0)
}

.zkAddressTooltipWrapper{
  max-width:150px
}

.zkAddressTooltipWrapper .el-tooltip__popper{
  background:unset !important;
  color:unset !important
}

.zkAddressTooltip{
  position:absolute;
  top:20px;
  left:-10px;
  color:rgba(255,255,255,.6) !important;
  background:rgba(0,6,32,.8) !important;
  border-radius:4px !important;
  border:unset !important;
  font-size:10px !important;
  padding:2px 5px !important;
  line-height:16px !important;
  width:unset !important
}

.ag-root-wrapper{
  border-radius:8px;
  box-shadow:6px 10px 12px rgba(0,0,0,.3);
  border:none !important;
  font-family:"Inter" !important
}

.ag-center-cols-container{
  font-family:"Inter"
}

.ag-body-viewport{
  background-color:var(--color-bg-fifth) !important
}

.ag-row{
  border:none;
  height:60px !important;
  background-color:var(--color-bg-fifth) !important;
  color:var(--color-text-secondary)
}

.ag-row-odd{
  background-color:var(--color-bg-fifth) !important
}

.ag-cell{
  display:flex;
  align-items:center;
  font-size:14px;
  font-weight:500;
  font-family:"Red Hat Mono";
  color:rgba(255,255,255,.8)
}

.ag-header-group-cell-label,.ag-header-cell-label{
  font-size:16px;
  font-weight:600;
  color:var(--color-text-secondary) !important
}

.ag-overlay-no-rows-center{
  color:var(--color-text-secondary) !important
}

.ag-header,.ag-header-row{
  height:80px !important;
  min-height:80px !important;
  background-color:var(--color-bg-fifth) !important;
  border:none;
  border-bottom:1px solid var(--color-border-secondary)
}

.ag-header-container .ag-header-cell:nth-of-type(2){
  margin-left:6px
}

.ag-header-cell-text{
  font-size:14px;
  font-family:red Hat Text;
  color:rgba(255,255,255,.8)
}

.ag-tooltip{
  font-family:"Inter" !important
}

body,html{
  font-family:"Inter" !important;
  font-size:16px !important
}

.ui,h1,h2,h3,h4,h5,h6{
  font-family:"Inter" !important
}

input[type=number]::-webkit-inner-spin-button,input[type=number]::-webkit-outer-spin-button{
  -webkit-appearance:none;
  margin:0
}

.loader-dot1,.loader-dot2,.loader-dot3{
  width:.2px;
  height:.2px;
  border:double;
  border-radius:50%;
  margin:2px
}

.loader-dot1{
  animation:jump 1.6s -0.32s linear infinite
}

.loader-dot2{
  animation:jump 1.6s -0.16s linear infinite
}

.loader-dot3{
  animation:jump 1.6s linear infinite
}

.font-red-hat-text{
  font-family:"Red Hat Text" !important
}

.font-red-hat-mono{
  font-family:"Red Hat Mono" !important
}

@keyframes jump{
  0%,80%,100%{
    transform:scale(0)
  }

  40%{
    transform:scale(1.5)
  }
}

